.navbar-logo {
  width: 100px;
  height: 70px;
}

.navbar-logo img {
  width: 100%;
  height: 100%;
}

.navbar-title:hover {
  color: black;
  transform: scale(1.3);
}

.navbar-offcanvas {
  position: relative;
  display: inline-block;
}

.offcanvas-button {
  background: none;
  border: none;
  border-radius: 5px;
  color: rgb(120, 117, 117);
  cursor: pointer;
  padding: 0;
  height: 55px;
}

/*
.offcanvas-button.rotated {
  transform: rotate(-180deg);
}
*/

.offcanvas-button svg {
  width: 50px;
  height: 50px;
}

/* line 클래스에 작대기 스타일 적용 */
.line {
  display: block;
  width: 30px;
  height: 2px;
  background-color: gray;
  margin: 4px 0;
  transition: transform 0.3s ease; /* transform 속성에 대한 트랜지션 추가 */
  transform-origin: center; /* 변환 중심을 가운데로 설정 */
}

/* 버튼이 호버 상태일 때 라인을 아래 위로 이동하는 효과 생성 */
.offcanvas-button.hovered .line:first-child {
  transform: translateY(6px); /* 첫 번째 라인을 아래로 이동 */
}

.offcanvas-button.hovered .line:last-child {
  transform: translateY(-6px); /* 마지막 라인을 위로 이동 */
}
